import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { TaskComponent } from './task/task.component';
import {routing} from './app.routing';
import { TaskAddComponent } from './task-add/task-add.component';
import { MatTabsModule } from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MatButtonModule, MatCheckboxModule} from '@angular/material';
import {hammerjs  } from 'hammerjs';
import {MatSidenavModule} from '@angular/material';
import {MatListModule} from '@angular/material';
import {MatSlideToggleModule} from '@angular/material';
import {MatToolbarModule} from '@angular/material';
import {MatInputModule} from '@angular/material';
import { AppSidebarComponent } from './app-sidebar/app-sidebar.component';
import {TaskService} from './task/service/task.service';
import { TaskIsDonePipe } from './task/pipe/task-is-done.pipe';
import {MatIconModule, MatDialogModule, MatCardModule, MatTooltipModule, MatSnackBarModule} from '@angular/material';
import { DeleteDialogComponent } from './delete-dialog/delete-dialog.component';
import { NgDragDropModule } from 'ng-drag-drop';
import { ChartsModule } from 'ng2-charts';
import { ChatRoomComponent } from './chat-room/chat-room.component';
import {ChatServiceService} from './chat-room/service/chat-service.service';

@NgModule({
  declarations: [
    AppComponent,
    TaskComponent,
    TaskAddComponent,
    AppSidebarComponent,
    TaskIsDonePipe,
    DeleteDialogComponent,
    ChatRoomComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    routing,
    FlexLayoutModule,
    MatInputModule,
    BrowserAnimationsModule,
    MatButtonModule,
    MatCheckboxModule,
    MatSidenavModule,
    MatListModule,
    MatSlideToggleModule,
    MatIconModule,
    MatToolbarModule, MatDialogModule, NgDragDropModule, MatCardModule, MatTooltipModule, MatSnackBarModule,
    ChartsModule, NgDragDropModule.forRoot()
  ],
  providers: [TaskService, ChatServiceService],
  bootstrap: [AppComponent],
 entryComponents: [DeleteDialogComponent]
})
export class AppModule { }



